<template>
  <div class="cart">
    <div class="header">
      <van-icon name="arrow-left" @click="$router.back()" />
      <p>
        购物车<span v-if="cartlist.goodsmsglist">({{ cartlist.goodsmsglist.length }})</span>
      </p>
      <p>
        <span @click="showdeletebtn = !showdeletebtn">编辑</span>
        <van-icon name="chat-o" />
      </p>
    </div>
    <div class="content" v-if="!cartlist.length">
      <p>你的购物车空空如也</p>
      <div class="btn" @click="$router.push({ path: '/' })">去逛逛</div>
    </div>
    <div class="resemble" v-if="!cartlist.length">
      <p>大家都在看</p>
    </div>

    <div class="goodslist" v-if="cartlist.length">
      <div class="card" v-for="(item, index) in cartlist" :key="index">
        <div class="input">
          <label>
            <input type="checkbox" :checked="item.isSelected" @click="
              toggleTodo({ id: item.id, isSelected: $event.target.checked })
            " />
            <div class="icon">
              <i class="fa fa-check" aria-hidden="true"></i>
            </div>
          </label>
        </div>
        <div class="msg">
          <div class="fr" v-lazy:background-image="item.src"></div>
          <div class="fl">
            <h3>{{ item.title }}</h3>
            <p>
              <span v-if="item.color">颜色：{{ item.color }}；</span><span v-if="item.size">规格：{{ item.size }}</span>
              <span v-if="!item.color && !item.size">无</span>
            </p>
            <div class="nums">
              <p>&yen;{{ item.price }}</p>
              <div class="num">
                <p
                  @click="item.nums === 1 ? subtractnum({ id: item.id, nums: 1 }) : subtractnum({ id: item.id, nums: item.nums-- })">
                  -
                </p>
                <p>{{ item.nums }}</p>
                <p @click="addnum({ id: item.id, nums: item.nums++ })">+</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="footer" v-if="cartlist.length">
      <div class="fl">
        <label>
          <input type="checkbox" :checked="allCheck" @click="checkedAllTodo($event.target.checked)" />
          <div class="icon">
            <i class="fa fa-check" aria-hidden="true"></i>
          </div>
        </label>
        <p>全选({{ selectedlength }})</p>
      </div>
      <div class="fr">
        <p v-if="showdeletebtn">&yen;{{ someCheck ? allPrice : "0.00" }}</p>
        <div class="btn" v-if="showdeletebtn" @click="confirmOrder">结算</div>
        <div class="btn" v-else @click="deleteTodo">删除</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      allprice: "0.00",
      showdeletebtn: true,
    };
  },
  computed: {
    ...mapState(["cartlist"]),

    selectedlength: function () {
      return this.cartlist.filter((o) => o.isSelected === true).length;
    },

    allCheck: function () {
      return this.cartlist.every((o) => o.isSelected);
    },

    someCheck: function () {
      return this.cartlist.some((o) => o.isSelected);
    },

    allPrice: function () {
      return this.cartlist
        .filter((o) => o.isSelected === true)
        .map((o) => o.price * o.nums)
        .reduce((total, currentValue) => Number(total) + Number(currentValue));
    },
    settleAccounts: function () {
      return this.cartlist.filter((o) => o.isSelected === true)
    }

  },
  methods: {
    ...mapMutations(["toggleTodo", "checkedAllTodo", "deleteTodo", "addnum", "subtractnum"]),
    confirmOrder: function () {
      localStorage.setItem("settleAccounts", [])
      this.$router.push({ path: '/confirmorder' })
      localStorage.setItem("settleAccounts", JSON.stringify(this.settleAccounts))
    }
  },
  watch: {
    cartlist: function (goodsmsglist) {
      localStorage.setItem("cartlist", JSON.stringify(goodsmsglist));
    },
  },
};
</script>

<style lang="less" scoped>
.cart {
  height: 100vh;
  background: #efefef;
  overflow-y: scroll;

  .header {
    width: 100%;
    height: 45rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1rem solid #d6d6d8;
    position: fixed;
    top: 0;
    left: 0;
    background: #fafafa;
    z-index: 9;

    i {
      font-size: 17.5rem;
      color: #5e5e5e;
      margin-left: 15rem;
    }

    p {
      &:nth-child(2) {
        font-size: 18rem;
        color: #5e5e5e;
        padding-left: 60rem;
      }

      &:nth-child(3) {
        font-size: 13rem;
        color: #727272;
        display: flex;
        align-items: center;
        margin-right: 15rem;

        span {
          i {
            margin: 0;
          }
        }
      }
    }
  }

  .content {
    width: 100%;
    height: 300rem;
    margin-top: 45rem;
    background: url(../assets/cart-bg1.png) no-repeat center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-bottom: 10rem;

    p {
      position: absolute;
      bottom: 60rem;
      font-size: 12rem;
      color: #5e5e5e;
    }

    .btn {
      width: 100rem;
      height: 34.5rem;
      position: absolute;
      bottom: 15rem;
      color: white;
      background: #ff5777;
      margin: 4rem 6rem;
      padding: 6rem 12.5rem;
      border-radius: 4rem;
      font-size: 15rem;
      text-align: center;
    }
  }

  .resemble {
    width: 100%;
    height: 725rem;
    background: url(../assets/cart-bg2.png) no-repeat 50%;
    background-size: 260rem 160rem;
    background-position: center 100rem;

    p {
      margin: 15rem 0 10rem 15rem;
      font-size: 14rem;
      color: #666;
    }
  }

  .goodslist {
    padding-top: 45rem;

    .card {
      display: flex;
      align-items: center;
      margin-bottom: 10rem;
      background: white;
      padding: 20rem 15rem;
      border-radius: 5rem;
      margin: 10rem;

      .input {
        label {
          margin-right: 5rem;

          input[type="checkbox"] {
            display: none;

            &:checked+.icon {
              background: #ff5777;
            }
          }

          div.icon {
            width: 20px;
            height: 20px;
            background-color: transparent;
            border: 1.5rem solid #eaeaea;
            overflow: hidden;
            border-radius: 25px;
            display: flex;
            justify-content: center;
            align-items: center;

            i {
              color: white;
            }
          }
        }
      }

      .msg {
        display: flex;

        .fr {
          width: 70rem;
          height: 100rem;
          margin: 0 10rem;
          flex-shrink: 0;
          background-size: cover;
          background-position: center top;
          background-repeat: no-repeat;
        }

        .fl {
          h3 {
            font-size: 14rem;
            font-weight: bold;
          }

          p {
            width: 100%;
            color: #afb0b5;
            background: #f7f7f7;
            font-size: 12rem;
            margin-top: 5rem;
            padding: 0 5rem;
            border-radius: 4rem;

            span {}
          }

          .nums {
            display: flex;
            margin-top: 5rem;

            p {
              background: white;
              font-size: 16rem;
              font-weight: bold;
              color: #ff3b5c;
            }

            .num {
              width: 150rem;
              display: flex;
              background: #f7f7f7;
              justify-content: center;
              align-items: center;
              border-radius: 10rem;
              margin-top: 5rem;

              p {
                color: #c9c1c4;
                background: #f7f7f7;
                padding: 0;
                margin: 0;
                text-align: center;

                &:nth-child(1) {
                  border-radius: 9rem 0 0 9rem;
                }

                &:nth-child(2) {
                  color: #333;
                }

                &:nth-child(3) {
                  border-radius: 0 9rem 9rem 0;
                }
              }
            }
          }
        }
      }
    }
  }

  .footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 45rem;
    background: white;
    z-index: 9;

    .fl {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-left: 20rem;

      label {
        margin-right: 5rem;

        input[type="checkbox"] {
          display: none;

          &:checked+.icon {
            background: #ff5777;
          }
        }

        div.icon {
          width: 20px;
          height: 20px;
          background-color: transparent;
          border: 1.5rem solid #eaeaea;
          overflow: hidden;
          border-radius: 25px;
          display: flex;
          justify-content: center;
          align-items: center;

          i {
            color: white;
          }
        }
      }

      p {
        color: #787690;
        font-size: 14rem;
      }
    }

    .fr {
      display: flex;
      align-items: center;
      margin-right: 25rem;

      p {
        font-size: 16rem;
        color: #ff5777;
      }

      div.btn {
        margin-left: 10rem;
        width: 60rem;
        height: 30rem;
        background: #ff5777;
        color: white;
        text-align: center;
        line-height: 30rem;
        font-size: 16rem;
        border-radius: 15rem;
      }
    }
  }
}
</style>